            h5 Calentamiento Global

            p La PNCC (2017) que es sus siglas      traduce: (poltica nacional de cambio climtico) establece que el calentamiento global es causado por las emisiones de gases de efecto invernadero generadas por distintas actividades humanas y coincide en afirmar que dicho cambio ha ocasionado impactos en sistemas humanos y naturales en todo el mundo. De continuar con la tendencia de emisiones, la temperatura promedio global aumentar en ms de 4 C y consecuentemente se incrementar la probabilidad de impactos climticos severos e irreversibles como la prdida de ecosistemas, inseguridad alimentaria, inundaciones, entre      otros. MINAMBIENTE (2017) Poltica Nacional de Cambio Climtico p.31

          .tarjeta.color-acento-botones.p-3(x="10%" y="60%" numero="")
