            p La CAR (Corporacin Autnoma Regional) define que la evaluacin de impacto ambiental es el resultado de medir y ponderar los efectos de las actividades del desarrollo humano o la carencia de acciones sobre distintos componentes del medio ambiente durante una etapa de planeacin. 
          .tarjeta.color-acento-botones.p-3(x="90%" y="60%" numero="")
            h5 Impacto ambiental

            p La CAR (corporacin Autnoma Regional)      establece que el impacto ambiental es cualquier alteracin en el medio fsico, qumico, biolgico, cultural y socioeconmico que pueda ser atribuido a actividades humanas relacionadas con las necesidades del proyecto.
          .tarjeta.color-acento-botones.p-3(x="70%" y="30%" numero="")
            h5 Contaminacin
            p Se entiende por contaminacin la alteracin del ambiente con sustancias o formas de energa puestas en l, por actividad humana o de la naturaleza, en cantidades, concentraciones o niveles capaces de interferir el bienestar y la salud de las personas, atentar contra la flora y la fauna, degradar la calidad del ambiente de los recursos de la nacin o de los particulares.
          .tarjeta.color-acento-botones.p-3(x="50%" y="80%" numero="")
            h5 Contaminante

            p Se entiende por contaminante cualquier elemento, combinacin de elementos, o forma de energa que actual o potencialmente pueda producir alteracin ambiental de las precedentemente descritas. La contaminacin puede ser fsica, qumica o biolgica.
