      h2 3.2 Componentes Ambientales
    p Aspectos ambientales que constituyen un medio (abitico o inerte, bitico o socioeconmico) como, por ejemplo, componente atmosfrico, hidrolgico, faunstico, demogrfico, entre otros.
    p En trminos generales, en el marco del proceso de licenciamiento ambiental los aspectos a evaluar para la determinacin del rea de influencia se deben plantear considerando una organizacin jerrquica de medio y componente, en la cual, los medios se entienden como la divisin general del ambiente y mxima categora de abordaje, y los componentes corresponden a los elementos ambientales que constituyen un medio, como se presenta a continuacin:
