          td  Clima: temperatura, altitud, precipitacin pluvial, humedad relativa.<br> Recurso hdrico: cursos de agua, quebradas, lagos, sistemas marinos.<br> Recurso suelo: Tipologa, edafologa, morfologa, fisiografa, uso actual de la tierra, capacidad de uso mayor, sismologa.<br> Recurso aire: Calidad del aire, nivel de ruidos, olores
        tr
