    h4.mt-4  rea de influencia directa (AID)
    p El rea de influencia directa es aquella donde se presentan los impactos generados en las fases de construccin y/o operacin de un proyecto, obra o actividad; est relacionada con el sitio donde se ubica la organizacin y su infraestructura. 
    p De acuerdo con el impacto generado el rea puede o no cambiar y de acuerdo a esto se deben delimitar las reas de influencia sobre todos los componentes. La caracterizacin del AID debe ofrecer una visin detallada de los componentes.   
