    h4  Ambiente Fsico
    p Incluye los componentes abiticos del ecosistema
    img(src="@/assets/curso/t3/14.svg")
    .row
      .col-lg-6
        .row.p-3.colort1
          h5.mb-0 Recursos Hdricos:
          p Se incluyen todos los aspectos de los recursos hdricos superficiales y subterrneos, empezando por sus caractersticas fsicas bsicas (redes de drenaje, canales y zonas inundables, riberas y batimetra de lagos y estuarios, la capa fretica y sus propiedades, el sistema de flujo de agua subterrnea, la      cantidad (caudales) y calidad (cuando existan datos). Al igual que los recursos terrestres, la descripcin incluye procesos y otras caractersticas dinmicas del medio ambiente, (por      ejemplo, diversidad biolgica en lagos, lagunas y ros, interacciones de las cadenas trficas) y el manejo del agua (abastecimiento, uso, derechos de uso, estndares de calidad, regulaciones, planes para administrar zonas costeras, etc.)
      .col-lg-6
        .row.p-3.colort2
          h5.mb-0 Recursos del aire:
          p.mb-5 Incluye una descripcin del clima y la meteorologa (balance del agua, estaciones, calidad del aire), as como los temas referidos al ruido (niveles de ruido ambiental, la existencia de fuentes de ruido, sensibilidad de los receptores, las reglamentaciones ambientales), olores, calor y luz, que se incluyen en la categora      de aire,      porque sus efectos son generalmente transmitidos a travs de la atmsfera.
          .row.mb-5
          .row.mb-3
