        .col-md-5.mb-4.mb-md-0
          p Referido fundamentalmente los ecosistemas existentes y la flora y fauna silvestre que habita en ellos.
          p Identifica las especies de flora y fauna silvestre caractersticas de la zona, los diferentes ecosistemas y zonas de vida, enfatizando la manera en que estos funcionan para garantizar la productividad biolgica y la diversidad (por ejemplo, cmo la vegetacin permite sostener la pesca y la vida silvestre,      cmo los hbitat de la vida silvestre soportan el ciclo de vida permitiendo la reproduccin, migracin, alimentacin y proteccin).
          p Describe tambin reas nicas, zonas en transicin (bordes), otros recursos de especial importancia, y el uso de los recursos biolgicos para la agricultura, silvicultura, pesca, etc.

        .col-md-6
