      .row.justify-content-center(titulo="Ambiente Socioeconmico")
        .col-md-5.mb-4.mb-md-0
          p Referido a las actividades humanas que se desarrollan dentro del mbito del proyecto, entre estas las actividades sociales, econmicas y culturales.

          p Esta categora incluye la poblacin humana (tamao, distribucin, etnia y otras caractersticas); actividades econmicas (empleo, inversin, ingreso promedio, impuestos, etc.) en el rea del proyecto. Usualmente, se incluye aqu una descripcin de la infraestructura socioeconmica, como los servicios e instalaciones pblicas y privadas, incluyendo el transporte, las viviendas, escuelas, parques, proteccin policial y contra incendio, servicios de salud, abastecimiento de agua y desage, manejo del agua de lluvia y desechos slidos.

          p La discusin sobre la infraestructura puede extenderse a temas como el trfico (donde el proyecto podra tener impacto en el corto o largo plazo sobre las vas de acceso o los niveles de trfico), los recursos energticos (por      ejemplo, el uso y conservacin de la energa en un proyecto) y los materiales peligrosos.
